(function() {
  'use strict';

  angular
    .module('neatClient')
    .controller('UsersController', UsersController);

  /** @ngInject */
  function UsersController(
    UserData,
    $log,
    $document,
    AuthService,
    UserDialog
  ) {
    var vm = this;
    var doc = $document[0];
    var users;
    var disabledUsers;
    var searchField;
    var showAdd = UserDialog.showAdd;

    vm.isAdmin = AuthService.isAdmin;
    vm.isSelf = AuthService.isSelf;
    vm.switchUsers = switchUsers;
    vm.showConfirm = UserDialog.showConfirm;
    vm.addOrDeleteAll = addOrDeleteAll;
    vm.changeRole = changeRole;
    vm.focusSearch = focusSearch;
    vm.roleClass = roleClass;
    vm.resetText = resetText;
    vm.btnDisabled = btnDisabled;

    activate();

    function activate() {
      angular.element(doc).ready(getSearchField);
      vm.showEnabled = true;
      return getUsers().then(successUsers);
      function getSearchField() {
        searchField = doc.getElementById('user-search');
      }
    }

    /////////////////////////////////
    // GET users
    // /////////////////////////////
    function getUsers() {
      return UserData.getUsers()
        .then(assignUsers(users));
    }

    function assignUsers(users) {
      return function(data) {
        users = data;
        vm.users = users;
        return vm.users;
      };
    }

    function successUsers() {
      var type = vm.showEnabled ? ' ' : ' disabled ';
      $log.info('Loaded' + type + 'users');
    }

    function getDisabledUsers() {
      return UserData.getDisabledUsers()
        .then(assignUsers(disabledUsers));
    }

    /////////////////////////////////
    // ngClick functions
    // /////////////////////////////
    function switchUsers() {
      vm.showEnabled = !vm.showEnabled;
      return vm.showEnabled
        ? getUsers().then(successUsers)
        : getDisabledUsers().then(successUsers);
    }

    function addOrDeleteAll(ev) {
      if (vm.showEnabled) {
        showAdd(ev, vm.users);
      } else {
        vm.showConfirm(ev, 0, 'delAll', { name: '' }, vm.users);
      }
    }

    function changeRole(user) {
      var privilege = user.privilege == 'A' ? 'M' : 'A';
      UserData.updateRole(user.id, privilege)
        .then(function() {
          user.privilege = privilege;
        });
    }

    /////////////////////////////////
    // DOM manipulation
    // /////////////////////////////
    function focusSearch() {
      searchField.focus();
    }

    /////////////////////////////////
    // Classes and Texts
    // /////////////////////////////
    function roleClass(privilege) {
      return privilege == 'A'
        ? 'md-warn'
        : 'md-accent md-hue-3';
    }

    function resetText(user) {
      if (user.disabled) return 'enable';
      if (user.active) return 'reset';
      return 'inactive';
    }

    function btnDisabled(user, button) {
      var isSelf = AuthService.isSelf;
      var isAdmin = AuthService.isAdmin;
      var isDisabled = isSelf(user.id) || !isAdmin() || user.id < 2;
      if (button == 'role') {
        isDisabled = user.disabled || isDisabled;
      } else if (button == 'reset') {
        isDisabled = isDisabled || !user.disabled && !user.active;
      }
      return isDisabled;
    }

  }
})();

(function() {
  'use strict';

  angular
    .module('neatClient')
    .factory('UserDialog', UserDialog);

  /** @ngInject */
  function UserDialog(
    $mdDialog,
    UserData,
    $document,
    $mdToast
  ) {
    var doc = $document[0];

    var service = {
      showConfirm: showConfirm,
      showAdd: showAdd
    };

    return service;

    function showConfirm(ev, idx, type, user, users) {
      var dialog = {
        del: {
          text: ' delete ',
          content: 'If the user has associated records, user will be disabled instead. Switch to disabled users view to perform hard delete.',
          ariaLabel: 'Confirm delete',
          ok: 'Delete',
          confirmAction: function confirmDelete() {
            UserData.deleteUser(user.id).then(function(data) {
              users.splice(idx, 1);
              confirmToast(data.message + ': ' + user.name);
            });
          }
        },
        delAll: {
          text: ' DELETE ALL ',
          content: 'Delete all disabled users. Are you sure?',
          ariaLabel: 'Confirm delete all',
          ok: 'Delete All',
          confirmAction: function confirmDelAll() {
            UserData.deleteAllDisabled().then(function(data) {
              users.length = 0;
              confirmToast(data.message + ': ' + data.affected);
            });
          }
        },
        reset: {
          text: ' reset ',
          content: 'Deactivate user and reset password. Once reset, user need to go through sign up process same as new user',
          ariaLabel: 'Confirm reset',
          ok: 'Reset',
          confirmAction: function confirmReset() {
            UserData.resetUser(user.id).then(function() {
              user.active = false;
              confirmToast('Reset ' + user.name);
            });
          }
        },
        enable: {
          text: ' enable ',
          content: 'Will restore user to original state (active or inactive)',
          ariaLabel: 'Confirm enable',
          ok: 'Enable',
          confirmAction: function confirmEnable() {
            UserData.enableUser(user.id).then(function() {
              users.splice(idx, 1);
              confirmToast('Enabled ' + user.name);
            });
          }
        }
      };

      function confirmTitle() {
        return 'Confirm to' + dialog[type].text + user.name;
      }

      var confirm = $mdDialog.confirm()
        .title(confirmTitle())
        .textContent(dialog[type].content)
        .ariaLabel(dialog[type].ariaLabel)
        .targetEvent(ev)
        .ok(dialog[type].ok)
        .cancel('Cancel');

      $mdDialog
        .show(confirm)
        .then(dialog[type].confirmAction);
    } // showConfirm

    function confirmToast(message) {
      return $mdToast.show(
        $mdToast.simple()
        .textContent(message)
        .position('bottom right')
        .hideDelay(2000)
      );
    }

    function showAdd(ev, users) {
      $mdDialog.show({
        controller: AddDialogController,
        templateUrl: 'app/users/add.dialog.html',
        parent: angular.element(doc.body),
        targetEvent: ev,
        clickOutsideToClose: true
      });

      function AddDialogController($scope, $mdDialog) {
        $scope.privilege = 'M';

        $scope.cancel = function() {
          $mdDialog.cancel();
        };

        $scope.add = function() {
          UserData.createUser({
            email: $scope.email,
            name: $scope.name,
            privilege: $scope.privilege
          }).then(function(data) {
            var user = data.user;
            users.push(user);
            confirmToast('Added user: ' + user.name);
            $mdDialog.hide();
          });
        };
      }
    } // showAdd

  } // UserDialog Service
})();

(function() {
  'use strict';

  angular
    .module('neatClient')
    .factory('UserData', UserData);

  /** @ngInject */
  function UserData(
    url,
    $q,
    $log,
    $http
  ) {

    var service = {
      getUsers: getUsers,
      getDisabledUsers: getDisabledUsers,
      createUser: createUser,
      resetUser: resetUser,
      updateRole: updateRole,
      enableUser: enableUser,
      deleteUser: deleteUser,
      deleteAllDisabled: deleteAllDisabled
    };

    return service;

    /////////////////////////////////
    // GET
    // /////////////////////////////
    function getUsers() {
      return $http.get(url('/users'))
        .then(getUsersComplete)
        .catch(catchErrorFn('XHR getUsers failed'));
    }

    function getDisabledUsers() {
      return $http.get(url('/users?disabled=1'))
        .then(getUsersComplete)
        .catch(catchErrorFn('XHR getDisabledUsers failed'));
    }

    /////////////////////////////////
    // Promise Fulfilled / Rejected
    // /////////////////////////////
    function getUsersComplete(data) {
      var users = data.data.users;
      return users;
    }

    function actionComplete(data) {
      return data.data;
    }

    function catchErrorFn(errMsg) {

      return function requestFailed(e) {
        var newMessage = errMsg;
        if (e.data && e.data.description) {
          newMessage += '\n' + e.data.description;
        }
        if (e.data) {
          e.data.description = newMessage;
        }
        $log.error(e.data);
        return $q.reject(e);
      };
    }

    /////////////////////////////////
    // POST
    // /////////////////////////////
    function createUser(user) {
      return $http.post(url('/users'), user)
        .then(actionComplete)
        .catch(catchErrorFn('XHR createUser failed'));
    }

    /////////////////////////////////
    // PUT
    // /////////////////////////////
    function resetUser(id) {
      return $http.put(url('/user', id), {
        password: 'null',
        active: 'No'
      }).then(actionComplete)
        .catch(catchErrorFn(actionErrMsg('resetUser', id)));
    }

    function updateRole(id, privilege) {
      return $http.put(url('/user', id), {
        privilege: privilege
      }).then(actionComplete)
        .catch(catchErrorFn(actionErrMsg('updateRole', id)));
    }

    function enableUser(id) {
      return $http.put(url('/user', id), {
        disabled: 'false'
      }).then(actionComplete)
        .catch(catchErrorFn(actionErrMsg('enableUser', id)));
    }

    /////////////////////////////////
    // DELETE
    // /////////////////////////////
    function deleteUser(id) {
      return $http.delete(url('/user', id))
        .then(actionComplete)
        .catch(catchErrorFn(actionErrMsg('deleteUser', id)));
    }

    function deleteAllDisabled() {
      return $http.delete(url('/users'))
        .then(actionComplete)
        .catch(catchErrorFn('XHR deleteAllDisabled failed'));
    }

    /////////////////////////////////
    // Helpers
    // /////////////////////////////
    function actionErrMsg(name, id) {
      return 'XHR ' + name + ' by ' + id + ' failed';
    }

  }
})();

(function() {
  'use strict';

  angular
    .module('neatClient')
    .factory('MapInit', MapInit);

  /** @ngInject */
  function MapInit(
    $window,
    ApiKey,
    $log,
    $q
  ) {
    var doc = $window.document;
    var asyncUrl = 'https://maps.googleapis.com/maps/api/js?key=';
    asyncUrl += ApiKey + '&libraries=places&callback=';
    var mapsDefer = $q.defer();

    $window.GMapInitialized = mapsDefer.resolve;

    function asyncLoad(asyncUrl, cbName) {
      $log.log('adding script tag');
      var script = doc.createElement('script');
      script.src = asyncUrl + cbName;
      doc.body.appendChild(script);
    }
    asyncLoad(asyncUrl, 'GMapInitialized');

    var service = {
      initialized: mapsDefer.promise,
      geocoder: geocoder
    };

    function geocoder(Gmap, latLng, onceLocated) {
      var gc = new Gmap.Geocoder;
      gc.geocode({ location: latLng }, geocodeCb);

      function geocodeCb(results, status) {
        if (status === Gmap.GeocoderStatus.OK) {
          if (results[0]) {
            onceLocated(results[0]);
          } else {
            $window.alert('No results found');
          }
        } else {
          $window.alert('Geocoder failed due to: ' + status);
        }
      }
    }

    return service;

  } // MapInit
})();

(function() {
  'use strict';

  angular
    .module('neatClient')
    .factory('MapDialog', MapDialog);

  /** @ngInject */
  function MapDialog(
    byId,
    MapData,
    MapInit,
    mapIcons,
    $document,
    $log,
    $mdDialog,
    $mdToast,
    $rootScope,
    $timeout,
    $window
  ) {
    var nav = $window.navigator;

    var service = {
      createInfoContent: createInfoContent,
      initMarkerDialog: initMarkerDialog,
      confirmDelete: confirmDelete
    };

    return service;

    function createInfoContent(record) {
      var address = '<h4>' + record.Location.address + '</h4>';
      var severity = '<p>Severity: ' + record.severity + '</p>';
      var description = '<p>' + (record.description || 'n/a') + '</p>';
      var divider = '<md-divider></md-divider>';
      var editBtn = '<button class="md-primary md-button" type="button"' +
        ' aria-label="Edit" id="edit-record-btn">Edit</button>';
      var delBtn = '<button class="md-primary md-button" type="button"' +
        ' aria-label="Delete" id="del-record-btn">Delete</button>';
      var content = address + severity + description +
                    divider + editBtn + delBtn;
      return content;
    }

    function initMarkerDialog(
      types,
      Gmap,
      neatMap,
      magicMarker
    ) {

      return function showDialog(ev, marker, infowindow, markers) {
        $mdDialog.show({
          controller: DialogController,
          templateUrl: 'app/map/marker.dialog.html',
          parent: angular.element($document[0].body),
          targetEvent: ev,
          clickOutsideToClose: true
        });

        function DialogController($scope, $mdDialog) {
          var vm = $scope;
          var record;
          var isUpdate = marker ? true : false;
          var isMapAdd;
          $timeout(addPlaceSearch);

          vm.types = types;
          vm.severity = 3;
          vm.cancel = cancel;
          vm.confirm = confirm;
          vm.locateCurrent = locateCurrent;

          if (isUpdate) {
            record = marker.myRecord;
            vm.lat = record.Location.latitude;
            vm.long = record.Location.longitude;
            vm.address = record.Location.address;
            vm.description = record.description;
            vm.severity = record.severity;
            vm.typeId = record.TypeId;
            // for adding directly on the map without clicking add button
            isMapAdd = !record.TypeId;
          }

          function addPlaceSearch() {
            var input = byId('address');
            var searchBox = new Gmap.places.SearchBox(input);

            neatMap.addListener('bounds_changed', function() {
              searchBox.setBounds(neatMap.getBounds());
            });

            searchBox.addListener('places_changed', function() {
              var places = searchBox.getPlaces();
              if (places.length === 0) return;
              var place = places[0];
              vm.address = place.name;
              vm.long = place.geometry.location.lng();
              vm.lat = place.geometry.location.lat();
            });
          }

          function cancel() {
            $mdDialog.cancel();
          }

          function confirm() {
            var recordVals = {
              long: vm.long,
              lat: vm.lat,
              address: vm.address,
              description: vm.description,
              severity: vm.severity,
              user_id: $rootScope.me.id,
              type_id: vm.typeId
            };

            if (isUpdate && !isMapAdd) {
              update(recordVals);
            } else {
              add(recordVals);
            }
            $mdDialog.hide();
          }

          function add(recordVals) {
            MapData.createRecord(recordVals)
              .then(function(data) {
                if (isMapAdd) {
                  infowindow.close();
                  confirmDelete(ev, marker, markers);
                }
                var newRecord = data.record;
                newRecord.Location = {
                  address: vm.address,
                  latitude: vm.lat,
                  longitude: vm.long
                };
                magicMarker(vm.lat, vm.long, newRecord, true);
              });
          }

          function update(recordVals) {
            MapData.updateRecord(record.id, recordVals)
              .then(function(data) {
                // closes infowindow
                infowindow.close();
                var newRecord = data.affected[1][0];
                newRecord.Location = {
                  address: vm.address,
                  latitude: vm.lat,
                  longitude: vm.long
                };
                marker.myRecord = newRecord;
                marker.setIcon(mapIcons(
                  types[newRecord.TypeId],
                  newRecord.severity,
                  Gmap
                ));
                marker.setPosition({
                  lat: vm.lat,
                  lng: vm.long
                });
              });
          }

          function locateCurrent() {
            vm.address = 'Locating...';
            vm.locating = true;
            if (nav.geolocation) {
              nav.geolocation.getCurrentPosition(function(pos) {
                vm.lat = pos.coords.latitude;
                vm.long = pos.coords.longitude;
                MapInit.geocoder(Gmap, {
                  lat: vm.lat, lng: vm.long
                }, autoFillAddress);
                function autoFillAddress(result) {
                  vm.locating = false;
                  vm.address = result.formatted_address;
                  vm.$apply();
                }
              });
            } else {
              $log.error('Browser does not support goelocation');
            }
          }

        } // dialogCtrl
      }; // showDialog

    } // initMarkerDialog

    function confirmDelete(ev, marker, markers) {
      var record = marker.myRecord;
      if (!record.TypeId) {
        return deleteUIcleanUp();
      }

      var address = record.Location.address;
      var text =
        'Inreversible. Are you sure you want to delete: ' + address + '?';
      var confirm = $mdDialog.confirm()
        .title('Confirm to delete')
        .textContent(text)
        .ariaLabel('Confirm delete')
        .targetEvent(ev)
        .ok('Delete')
        .cancel('Cancel');

      $mdDialog
        .show(confirm)
        .then(performDelete);

      function performDelete() {
        MapData.deleteRecord(marker.myRecord.id)
          .then(deleteUIcleanUp);
      }

      function deleteUIcleanUp(data) {
        marker.setMap(null);
        var idx = markers.indexOf(marker);
        if (idx > -1) {
          markers.splice(idx, 1);
        }
        if (data) {
          $log.info(data);
        }
      }
    } // confirmDelete


  } // MapDialog Service
})();

(function() {
  'use strict';

  angular
    .module('neatClient')
    .factory('MapData', MapData);

  /** @ngInject */
  function MapData(
    url,
    $q,
    $log,
    $http
  ) {

    var service = {
      getTypes: getTypes,
      getRecords: getRecords,
      getFilteredRecords: getFilteredRecords,
      createRecord: createRecord,
      updateRecord: updateRecord,
      deleteRecord: deleteRecord
    };

    return service;

    /////////////////////////////////
    // GET
    // /////////////////////////////
    function getTypes() {
      return $http.get(url('/types'))
        .then(getDataComplete('types'))
        .catch(catchErrorFn('XHR getTypes failed'));
    }

    function getRecords() {
      return $http.get(url('/records'))
        .then(getDataComplete('records'))
        .catch(catchErrorFn('XHR getRecords failed'));
    }

    function getFilteredRecords(chosenTypes, severity) {
      var queryStr = '?';

      if (chosenTypes.length > 0) {
        chosenTypes = chosenTypes.join();
        queryStr += 'ftypes=' + chosenTypes;
        queryStr += severity > 0 ? '&' : '';
      }

      if (severity > 0) {
        queryStr += 'fseverity=' + severity;
      }

      return $http.get(url('/records' + queryStr))
        .then(getDataComplete('records'))
        .catch(catchErrorFn('XHR getFilteredRecords failed'));
    }

    /////////////////////////////////
    // Promise Fulfilled / Rejected
    // /////////////////////////////
    function getDataComplete(type) {
      return function complete(data) {
        return data.data[type];
      };
    }

    function actionComplete(data) {
      return data.data;
    }

    function catchErrorFn(errMsg) {

      return function requestFailed(e) {
        var newMessage = errMsg;
        if (e.data && e.data.description) {
          newMessage += '\n' + e.data.description;
        }
        if (e.data) {
          e.data.description = newMessage;
        }
        $log.error(e.data);
        return $q.reject(e);
      };
    }

    /////////////////////////////////
    // POST
    // /////////////////////////////
    function createRecord(record) {
      return $http.post(url('/records'), record)
        .then(actionComplete)
        .catch(catchErrorFn('XHR createRecord failed'));
    }

    /////////////////////////////////
    // PUT
    // /////////////////////////////
    function updateRecord(id, record) {
      return $http.put(url('/record', id), record)
        .then(actionComplete)
        .catch(catchErrorFn(actionErrMsg('updateRecord', id)));
    }

    /////////////////////////////////
    // DELETE
    // /////////////////////////////
    function deleteRecord(id) {
      return $http.delete(url('/record', id))
        .then(actionComplete)
        .catch(catchErrorFn(actionErrMsg('deleteRecord', id)));
    }

    /////////////////////////////////
    // Helpers
    // /////////////////////////////
    function actionErrMsg(name, id) {
      return 'XHR ' + name + ' by ' + id + ' failed';
    }

  }
})();

(function() {
  'use strict';

  angular
    .module('neatClient')
    .controller('MapController', MapController);

  /** @ngInject */
  function MapController(
    byId,
    MapData,
    MapDialog,
    mapIcons,
    MapInit,
    $log,
    $mdDialog,
    $mdSidenav,
    $rootScope,
    $scope,
    $window
  ) {
    var vm = this;
    var markers = [];
    var Gmap;
    var neatMap;
    var mapEl;
    var clearing = false; // for filterSeverity watch
    var navigator = $window.navigator;

    vm.toggleSideMenu = toggleSideMenu;
    vm.filterUpdate = filterUpdate;
    vm.filterClearAll = filterClearAll;
    vm.getLocalMap = getLocalMap;

    activate();

    function activate() {
      mapEl = byId('map');
      MapInit.initialized
      .then(setupMap)
      .then(setupControls);
    }

    /////////////////////////////////
    // Google Map Setup
    // /////////////////////////////
    function getLocalMap() {
      vm.locating = true;
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(function(pos) {
          vm.locating = false;
          $rootScope.mapView = {
            center: {
              lat: pos.coords.latitude,
              lng: pos.coords.longitude
            },
            zoom: 15
          }
          setupMap();
        });
      } else {
        $log.error('Browser does not support goelocation');
      }
    }

    function setupMap() {
      Gmap = $window.google.maps;
      neatMap = new Gmap.Map(mapEl, $rootScope.mapView);

      neatMap.addListener('idle', function() {
        $rootScope.mapView = {
          center: neatMap.getCenter(),
          zoom: neatMap.getZoom()
        }
      });

      clickMapToAddListener();

      return getTypes()
        .then(getRecords)
        .then(assignRecordsToMarkers);
    }

    function clickMapToAddListener() {
      neatMap.addListener('click', onClickAddMarker);

      function onClickAddMarker(ev) {
        MapInit.geocoder(Gmap, ev.latLng, addPlaceholder);
      }
    } // clickMapToAddListener

    /*
     * Map helpers
     */
    function addPlaceholder(result) {
      var resetTime = 180000; // 3 min
      var geo = result.geometry.location;
      var lat = geo.lat();
      var lng = geo.lng();
      var record = {
        Location: {
          address: result.formatted_address,
          latitude: lat,
          longitude: lng
        },
        severity: 3
      };
      var marker = magicMarker(lat, lng, record, true, true);
      setTimeout(function clearMarkerIfTooLong() {
        MapDialog.confirmDelete(0, marker, markers);
      }, resetTime);
    }

    function assignRecordsToMarkers(records) {
      records.forEach(function(record) {
        var local  = record.Location;
        magicMarker(local.latitude, local.longitude, record);
      });
      return records;
    }

    function magicMarker(lat, lng, record, animate, tbd) {
      var type = tbd ? 'placeholder' : vm.types[record.TypeId];
      var options = {
        position: { lat: lat, lng: lng },
        icon: mapIcons(type, record.severity, Gmap),
        map: neatMap,
        myRecord: record,
        infowindow: null
      };

      if (animate) {
        options.animation = Gmap.Animation.DROP;
      }

      var marker = new Gmap.Marker(options);
      attachMarkerEvent(marker);
      markers.push(marker);

      return marker;
    }

    function clearMarkers() {
      markers.forEach(function loopMarkers(marker) {
        marker.setMap(null);
      });

      markers = [];
    }

    function attachMarkerEvent(marker) {
      marker.addListener('click', function() {
        var self = this;

        if (self.infowindow) {
          var infowindow = self.infowindow;
          self.infowindow = null;
          return infowindow.close();
        }

        // on click create a new info window with record content
        self.infowindow = new Gmap.InfoWindow({
          content: MapDialog.createInfoContent(self.myRecord)
        });

        // add click listener to info window buttons
        self.infowindow.addListener('domready', function() {
          var editBtn = byId('edit-record-btn');
          var delBtn = byId('del-record-btn');

          editBtn.addEventListener('click', function(ev) {
            vm.showDialog(ev, self, self.infowindow, markers);
          });

          delBtn.addEventListener('click', function(ev) {
            MapDialog.confirmDelete(ev, self, markers);
          });
        });

        return self.infowindow.open(neatMap, self);
      });
    }

    /////////////////////////////////
    // GET
    // /////////////////////////////
    function getTypes() {
      return MapData.getTypes()
      .then(function(data) {
        vm.types = data.reduce(typeArrayToMap, {});
        vm.filterTypes = data.map(typeArrayToFilterModel);
        return vm.types;
      });

      function typeArrayToMap(prev, curr) {
        prev[curr.id] = curr.name;
        return prev;
      }

      function typeArrayToFilterModel(type) {
        type.checked = false;
        return type;
      }
    }

    function getRecords() {
      return MapData.getRecords()
        .then(function(data) {
          return data;
        });
    }

    /////////////////////////////////
    // Dialog setup
    // /////////////////////////////
    function setupControls() {
      vm.showDialog = MapDialog.initMarkerDialog(
        vm.types,
        Gmap,
        neatMap,
        magicMarker
      );

      $scope.$watch('mp.filterSeverity', function(now) {
        if (now && !clearing) {
          vm.applySeverity = true;
        }
        clearing = false;
      });
    }

    /////////////////////////////////
    // Filter Menu
    // /////////////////////////////
    function toggleSideMenu() {
      return $mdSidenav('left').toggle();
    }

    function filterUpdate() {
      var chosenTypes = vm.filterTypes.reduce(toTypeIdArray, []);
      // reset markers before update
      clearMarkers();
      // if nothing is chosen, do nothing
      if (chosenTypes.length === 0 && !vm.applySeverity) {
        return getRecords()
          .then(assignRecordsToMarkers);
      }
      // selecting all types is the same as selecting none: return all
      if (chosenTypes.length === vm.filterTypes.length) {
        chosenTypes = [];
      }
      // severity 0 to prevent applying severity filter for MapData service
      var fSeverity = vm.applySeverity ? vm.filterSeverity : 0;

      return MapData.getFilteredRecords(chosenTypes, fSeverity)
        .then(assignRecordsToMarkers);

      function toTypeIdArray(types, curr) {
        if (curr.checked) {
          types.push(curr.id);
        }
        return types;
      }
    }

    function filterClearAll() {
      clearing = true;
      vm.filterTypes.forEach(clearAllTypes);
      vm.filterSeverity = 1;
      vm.applySeverity = false;

      function clearAllTypes(type) {
        type.checked = false;
      }
      return;
    }

  }
})();

(function() {
  'use strict';

  angular
    .module('neatClient')
    .controller('AuthController', AuthController);

  /** @ngInject */
  function AuthController(
    AuthService,
    $mdDialog,
    $scope,
    $state
  ) {
    var vm = this;

    vm.login = login;
    vm.join = joinNeat;
    vm.enter = enter;

    function login(ev) {
      var isValid = $scope.loginForm
        ? $scope.loginForm.$valid
        : $scope.joinForm.$valid;

      if (isValid) {
        AuthService.login(vm.email, vm.password)
          .then(function() {
            AuthService.getMe().then(function() {
              $state.go('map');
            });
          }).catch(function(err) {
            showInvalidDialog(ev, err.message);
          });
      } else {
        showInvalidDialog(ev, 'One or more fields invalid');
      }
    }

    function joinNeat(ev) {
      AuthService.register(
        vm.email,
        vm.name,
        vm.password,
        vm.confirm
      ).then(function(res) {
        if (res.user.active) {
          login(ev);
        }
      }).catch(function(err) {
        showInvalidDialog(ev, err.data.message);
      });
    }

    function enter(ev, join) {
      if (ev.charCode !== 13) return;
      if (join) {
        return joinNeat();
      }
      return login(ev);
    }

    function showInvalidDialog(ev, message) {
      var dialog = $mdDialog.confirm()
        .clickOutsideToClose(true)
        .title('Login Failed')
        .textContent(message)
        .ariaLabel('Invalid login warning dialog')
        .targetEvent(ev)
        .ok('Try Again')
        .cancel('Ok');

      $mdDialog.show(dialog).then(function() {
        vm.password = '';
      });
    }

  }
})();

