(function() {
  'use strict';

  angular
    .module('neatClient')
    .factory('AuthToken', AuthToken);

  /** @ngInject */
  function AuthToken(
    $window
  ) {

    var service = {
      getToken: getToken,
      setToken: setToken
    };

    return service;

    function getToken() {
      return $window.localStorage.getItem('token');
    }

    function setToken(token) {
      if (token) {
        $window.localStorage.setItem('token', token);
      } else {
        $window.localStorage.removeItem('token');
      }
    }

  } // AuthToken
})();

(function() {
  'use strict';

  angular
    .module('neatClient')
    .factory('AuthService', AuthService);

  /** @ngInject */
  function AuthService(
    AuthToken,
    $http,
    $q,
    $log,
    $rootScope,
    url
  ) {

    var service = {
      register: register,
      login: login,
      logout: logout,
      isLoggedIn: isLoggedIn,
      isAdmin: isAdmin,
      isSelf: isSelf,
      getMe: getMe
    };

    return service;

    function register(email, name, password, confirm) {
      return $http.put(url('/register'), {
        email: email,
        name: name,
        password: password,
        confirm: confirm
      }).then(registrationComplete)
        .catch(registrationFailed);

      function registrationComplete(data) {
        return data.data;
      }

      function registrationFailed(e) {
        $log.error(e.data);
        return $q.reject(e);
      }
    }

    function login(email, password) {
      // make sure to clear user, useful for register
      logout();

      return $http.post(url('/authenticate'), {
        email: email,
        password: password
      }).then(authSuccess)
        .catch(authFailed);

      function authSuccess(data) {
        AuthToken.setToken(data.data.token);
        return data.data;
      }

      function authFailed(e) {
        AuthToken.setToken();
        $log.error(e.data);
        return $q.reject(e.data);
      }
    }

    function logout() {
      AuthToken.setToken();
      $rootScope.me = {};
    }

    function isLoggedIn() {
      return AuthToken.getToken() ? true : false;
    }

    function isAdmin() {
      return Boolean($rootScope.me) && $rootScope.me.privilege == 'A';
    }

    function isSelf(id) {
      return $rootScope.me && $rootScope.me.id == id;
    }

    function getMe() {
      return $http.get(url('/me'))
        .then(getMeComplete)
        .catch(getMeFailed);

      function getMeComplete(data) {
        var me = data.data.user;
        $rootScope.me = me;
        $log.info('Added me');
        return me;
      }

      function getMeFailed(e) {
        $log.error(e.data);
        return $q.reject(e);
      }
    }

  } // AuthService
})();

(function() {
  'use strict';

  angular
    .module('neatClient')
    .factory('AuthInterceptor', AuthInterceptor);

  /** @ngInject */
  function AuthInterceptor(
    $q,
    AuthToken
  ) {

    var service = {
      request: request,
      responseError: responseError
    };

    return service;

    function request(config) {
      var token = AuthToken.getToken();
      if (token) {
        config.headers.Authorization = 'Bearer ' + token;
      }
      return config;
    }

    function responseError(res) {
      if (res.status == 403) {
        AuthToken.setToken();
      }
      return $q.reject(res);
    }

  } // AuthInterceptor
})();

(function() {
  'use strict';

  angular
    .module('neatClient')
    .run(runBlock);

  /** @ngInject */
  function runBlock(
    $log,
    $rootScope,
    $state,
    AuthService
  ) {

    // map defaults
    $rootScope.mapView = {
      center: {
        lat: 44.4120908,
        lng: -79.6701331
      },
      zoom: 15
    }

    $rootScope.$on('$stateChangeStart', watchAuth);

    function watchAuth(e, toState) {
      var isLoggedIn = AuthService.isLoggedIn();

      if (toState.name === 'join') {
        return;
      }

      if (toState.name === 'login') {
        if (isLoggedIn) {
          e.preventDefault();
          $state.go('map');
        }
        return;
      }

      if (isLoggedIn) {
        if (!$rootScope.me || !$rootScope.me.privilege) {
          AuthService.getMe().then(function() {
            $log.info('Readded me');
          });
        }
      } else {
        e.preventDefault();
        $state.go('login');
      }
    }

    $log.debug('runBlock end');
  }

})();

